import React, { useState } from 'react'

import firebase, { firestore } from 'firebase/app';
import 'firebase/auth'
import 'firebase/firestore'

import { Form, Button, Col, Alert, Spinner, Modal } from 'react-bootstrap';
import { useHistory } from 'react-router-dom';

export default function App() {

    const history = useHistory()

    const [validated, setValidated] = useState(false);
    const [loading, setLoading] = useState(false)
    const [error, setError] = useState("")
    const [show, setShow] = useState(false)

    const [groups, setGroupes] = useState("")
    const [register, setRegister] = useState({
        email: "",
        passwd: "",
        lastname: "",
        firstname: "",
        username: "",
        city: "",
        synopsis: ""
    })

    const handleChange = (event) => {
        setRegister({
            ...register,
            [event.target.name]: event.target.value
        })
    }

    const createNewUser = (event) => {

        const form = event.currentTarget;

        if (form.checkValidity()) {
            setLoading(true)
            firestore().collection('users').where("username", "==", register.username).get()
                .then((data) => {
                    if (data.size === 0) {
                        firebase.auth()
                            .createUserWithEmailAndPassword(register.email, register.passwd)
                            .then(authUser => {
                                // Create a user in your Firebase Realtime database
                                return firestore().collection('users').doc(authUser.user.uid)
                                    .set({
                                        ...register,
                                        passwd: '',
                                        groups: groups.split(","),
                                        dateCreatedAccount: firebase.firestore.Timestamp.fromDate(new Date()),
                                    });
                            })
                            .then(() => {
                                return firebase.auth().currentUser.sendEmailVerification({
                                    url: process.env.REACT_APP_CONFIRMATION_EMAIL_REDIRECT,
                                })
                            })
                            .then(() => {
                                return firebase.auth().currentUser.updateProfile({
                                    displayName: register.username,
                                });
                            })
                            .then(() => {
                                history.push("/paroletahitienne/")
                            })
                            .catch(error => {
                                if (error.code === ERROR_CODE_ACCOUNT_EXISTS)
                                    setError(ERROR_MSG_ACCOUNT_EXISTS);
                            });
                    }
                    else {
                        setError(ERROR_MSG_USERNAME_EXISTS)
                        setLoading(false)
                    }
                })
        }
        setValidated(true);
        event.preventDefault();
    }

    const handleShowCondition = () => {
        setShow(true)
    }

    const handleClose = () => {
        setShow(false)
    }

    return (
        <Form className="mb-4" noValidate validated={validated} onSubmit={createNewUser}>
            <Form.Row>
                <Form.Group as={Col} controlId="formGridEmail">
                    <Form.Label>Email</Form.Label>
                    <Form.Control required name="email"
                        value={register.email} onChange={handleChange}
                        type="email" placeholder="parole@tahitienne.com" />
                </Form.Group>

                <Form.Group as={Col} controlId="formGridPassword">
                    <Form.Label>Password</Form.Label>
                    <Form.Control required name="passwd"
                        value={register.passwd} onChange={handleChange}
                        type="password" placeholder="petea" />
                </Form.Group>
            </Form.Row>

            <Form.Row>
                <Form.Group as={Col} controlId="formGridCity">
                    <Form.Label>Nom</Form.Label>
                    <Form.Control required name="lastname"
                        value={register.lastname} onChange={handleChange}
                        type="text" placeholder=""
                    />
                </Form.Group>

                <Form.Group as={Col} controlId="formGridCity">
                    <Form.Label>Prénom</Form.Label>
                    <Form.Control required name="firstname"
                        value={register.firstname} onChange={handleChange}
                        type="text" placeholder=""
                    />
                </Form.Group>
            </Form.Row>
            <Form.Row>
                <Form.Group as={Col} controlId="formGridCity">
                    <Form.Label>Ville</Form.Label>
                    <Form.Control required name="city"
                        value={register.city} onChange={handleChange}
                        type="text" placeholder="Paea"
                    />
                </Form.Group>

                <Form.Group as={Col} controlId="formGridZip">
                    <Form.Label>Nom utilisateur</Form.Label>
                    <Form.Control required name="username"
                        type="text"
                        value={register.username} onChange={handleChange}
                    />
                </Form.Group>
            </Form.Row>

            <hr />

            <Form.Row>
                <Form.Group as={Col} controlId="formGridCity">
                    <Form.Label>Groupe</Form.Label>
                    <Form.Control name="groups"
                        value={groups} onChange={e => setGroupes(e.target.value)}
                        type="text" placeholder="Kiti,Koto,Kata"
                    />
                </Form.Group>
            </Form.Row>

            <Form.Group as={Col} controlId="formGridZip">
                <Form.Label>vous concernant</Form.Label>
                <Form.Control
                    name="synopsis"
                    as="textarea"
                    value={register.synopsis} onChange={handleChange}
                />
            </Form.Group>

            <Form.Group id="formGridCheckbox">
                <Button variant="link"
                    onClick={handleShowCondition} size="sm">
                    conditions d'utilisation
                    </Button>
                <Form.Check required className="text-left" type="checkbox" label="J'accepte" />
            </Form.Group>

            <Button variant={loading ? 'link' : 'primary'} type="submit" disabled={loading}>
                {loading ? <Spinner
                    as="span"
                    animation="grow"
                    size="sm"
                    role="status"
                    aria-hidden="true"
                /> : 'Inscription'}
            </Button>
            {error && <Alert className="mt-2" variant="danger">{error}</Alert>}

            <Modal scrollable show={show} onHide={handleClose}>
                <Modal.Header closeButton>
                    <Modal.Title>Parole Tahitienne</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <h2>Conditions d'utilisation</h2>
                    <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Porro veniam quis dolorem ipsa sapiente ex, hic obcaecati ducimus culpa. Doloribus itaque ducimus temporibus impedit tempora, totam, qui aspernatur nihil rem atque assumenda officiis officia ad sunt deserunt doloremque optio in quibusdam quaerat? Optio minus fugit id consequuntur blanditiis voluptates necessitatibus. Praesentium totam ut eos sint pariatur fugiat nulla nam perspiciatis velit! Illo explicabo aut, veniam veritatis quia sit accusamus. Fugit, dicta earum quibusdam ex quia quod esse aut distinctio saepe dolor sunt itaque facere ut quasi alias velit odio repellendus molestiae voluptatem suscipit. Atque in, repellendus dolore ea beatae impedit rem eum hic sit aspernatur saepe laboriosam facilis suscipit odio, dolores similique provident, molestiae neque enim doloremque recusandae cumque id.</p>
                </Modal.Body>
                <Modal.Footer>
                    <Button variant="secondary" onClick={handleClose}>
                        Close
                    </Button>
                </Modal.Footer>
            </Modal>
        </Form>
    )
}

const ERROR_CODE_ACCOUNT_EXISTS = 'auth/email-already-in-use';
const ERROR_MSG_ACCOUNT_EXISTS = 'Cet email est déjà utilisé!';
const ERROR_MSG_USERNAME_EXISTS = 'Ce nom utilisateur est déjà utilisé!';
