import React, { useState } from 'react'
import firebase from 'firebase/app';
import 'firebase/auth'
import 'firebase/firestore'
import { useAuthState } from 'react-firebase-hooks/auth';

import { Button, Row, Form, Col, Modal, Alert, ButtonToolbar, Dropdown } from 'react-bootstrap'
import { Link } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faUserCog } from '@fortawesome/free-solid-svg-icons';

export default function App() {
    const [user] = useAuthState(firebase.auth())
    return (
        <span>
            {!user && <LogIn />}
            {user && <LogOut />}
        </span>
    )
}

function LogIn() {
    const [error, setError] = useState("")
    const [register, setRegister] = useState({
        email: "",
        passwd: "",
        lastname: "",
        firstname: "",
        username: ""
    })

    const handleChange = (e) => {
        setRegister({
            ...register,
            [e.target.name]: e.target.value
        })
    }

    const [showLogin, setShowLogin] = useState(false);
    const handleCloseLogin = () => setShowLogin(false);
    const handleShowLogin = () => setShowLogin(true);

    const signInWithEmail = (event) => {
        firebase.auth().signInWithEmailAndPassword(register.email, register.passwd)
            .then(() => {
                setShowLogin(false)
                setError("")
            })
            .catch(error => {
                if (error.code === ERROR_CODE_ACCOUNT_NOEXISTS)
                    setError(error.message)
            })
        event.preventDefault()
    }

    return (
        <span>
            <ButtonToolbar>
                <Button className="ml-1" onClick={handleShowLogin} variant="link">Se connecter</Button>
            </ButtonToolbar>

            <Modal show={showLogin} onHide={handleCloseLogin}>
                <Modal.Header closeButton>
                    <Modal.Title>Connectez-vous</Modal.Title>
                </Modal.Header>
                <Form onSubmit={signInWithEmail}>
                    <Modal.Body>
                        <Form.Group as={Row} controlId="formPlaintextEmail">
                            <Form.Label column sm="2">
                                Identifiant
                                    </Form.Label>
                            <Col sm="10">
                                <Form.Control value={register.email} name="email" onChange={handleChange} type="email" />
                            </Col>
                        </Form.Group>

                        <Form.Group as={Row} controlId="formPlaintextPassword">
                            <Form.Label column sm="2">
                                Mot de passe
                                    </Form.Label>
                            <Col sm="10">
                                <Form.Control type="password" value={register.passwd} name="passwd" onChange={handleChange} />
                            </Col>
                        </Form.Group>
                    </Modal.Body>
                    <Modal.Footer>
                        <Button variant="secondary" onClick={handleCloseLogin}>
                            Fermer
                                </Button>
                        <Button variant="primary" type="submit">
                            S'identifier
                                </Button>
                        {error &&
                            <Alert variant="danger">{error}</Alert>
                        }
                    </Modal.Footer>
                </Form>
                <ButtonToolbar>
                    <Button size="sm" as={Link} to={"/paroletahitienne/signup"} onClick={handleCloseLogin} variant="link">Créer un compte</Button>
                </ButtonToolbar>
            </Modal>
        </span>
    )
}

function LogOut() {
    const handleClick = () => {
        firebase.auth().signOut()
    }
    return (
        <ButtonToolbar>
            <Dropdown>
                <Dropdown.Toggle variant="link" id="dropdown-custom-1">
                    <FontAwesomeIcon
                        icon={faUserCog}
                        size="lg"
                    />
                </Dropdown.Toggle>
                <Dropdown.Menu className="dropdown-menu-md-right">
                    <Dropdown.Item as={Link} to="/paroletahitienne/profile" eventKey="1">Profile</Dropdown.Item>
                    <Dropdown.Divider />
                    <Dropdown.Item onClick={handleClick} eventKey="4">Se Déconnecter</Dropdown.Item>
                </Dropdown.Menu>
            </Dropdown>
        </ButtonToolbar>
    )
}

const ERROR_CODE_ACCOUNT_NOEXISTS = "auth/user-not-found"