import 'bootstrap/dist/css/bootstrap.min.css';

import React from 'react'
import { BrowserRouter, Route, Switch } from 'react-router-dom';

import { Firebase } from "../Firebase/index"
import Navigation from "../Navigation/index";
import Home from "../Home/index"
import Liste from "../Liste/index"
import Parole from "../Parole/index"
import Profile from "../Profile/index"
import Edite from "../Edition/index"
import SignUp from "../SignUp/index"

function App() {
    return (
        <BrowserRouter>
            <Firebase>
                <Navigation />
                <div className="container">
                    <Switch>
                        <Route exact path="/paroletahitienne/" component={Home} />
                        <Route exact path="/paroletahitienne/liste" component={Liste} />
                        <Route exact path="/paroletahitienne/liste=:id" component={Parole} />
                        <Route exact path="/paroletahitienne/profile" component={Profile} />
                        <Route exact path="/paroletahitienne/edit=:id" component={Edite} />
                        <Route exact path="/paroletahitienne/signup" component={SignUp} />
                    </Switch>
                </div>
            </Firebase>
        </BrowserRouter>
    )
};

export default App
