import React from 'react'
import { useParams } from 'react-router-dom';
import firebase from 'firebase/app'
import 'firebase/firestore'
import { useDocument } from 'react-firebase-hooks/firestore';

import { Alert, Spinner, Jumbotron, Container, Button } from 'react-bootstrap';
import { Link } from 'react-router-dom'

export default function Parole() {
    const { id } = useParams()
    const [value, loading, error] = useDocument(
        firebase.firestore().doc(`paroles/${id}`),
        {
            snapshotListenOptions: { includeMetadataChanges: true },
        }
    )

    return (
        <div>
            {error && <Alert variant="danger">Erreur: {error}</Alert>}
            {loading && <Spinner animation="grow" variant="primary"></Spinner>}
            {value && (
                <Jumbotron fluid>
                    <Button as={Link} to={`/paroletahitienne/edit=${id}`} variant="link">editer</Button>
                    <Container>
                        <h1>{value.data().titre}</h1>
                        <pre>
                            {value.data().parole}
                        </pre>
                    </Container>
                </Jumbotron>
            )}
        </div>
    )
}